.ChartBar__controller{
    
    display: flex;
    flex-direction: column;
    margin: 1.5%;
    
    
}

.Chart-bar{
    background-color: aliceblue;
    width: 8px;
    align-self: center;
    display: flex;
    flex-direction: column-reverse;
    border-top-left-radius:10px;
    border-top-right-radius:10px;
}

.ChartBar__filled{
    background-color: rgb(44, 6, 49);
    width:8px;
    margin: 0px;
    padding: 0px;
    border-top-left-radius:10px;
    border-top-right-radius:10px;
}